html,
body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}
#app {
  min-width: 550px;
  max-width: 900px;
  flex: 1;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.05);
}
p {
  text-align: justify;
}
ul {
  padding-left: 20px;
}
code {
  font-size: 15px;
}
table {
  max-width: 800px;
  border: 2px solid #fff;
  border-collapse: collapse;
  text-align: center;
}
table td,
table th {
  padding: 3px 10px;
  border: 2px solid #fff;
  white-space: nowrap;
}
table td:last-child {
  text-align: left;
  word-break: break-word;
  white-space: normal;
}
.root {
  padding: 20px;
}
.root .top-block {
  text-align: center;
}
.root .top-block #carousel {
  height: 100px;
  position: relative;
  margin-top: 50px;
}
.root .top-block object {
  max-width: 150px;
  height: 20px;
}
.present-toast.shrink .main-content {
  -webkit-animation-duration: inherit;
          animation-duration: inherit;
  -webkit-animation-name: animeShrink;
          animation-name: animeShrink;
}
.present-toast.tremble .main-content {
  -webkit-animation-duration: inherit;
          animation-duration: inherit;
  -webkit-animation-name: animeTremble;
          animation-name: animeTremble;
}
.custom-toast.search .icon {
  background-image: url('data:image/svg+xml;base64,');
}
.custom-progress-toast.shake .main-content {
  -webkit-animation-duration: inherit;
          animation-duration: inherit;
  -webkit-animation-name: animeShake;
          animation-name: animeShake;
}
@-webkit-keyframes animeShake {
  25% {
    transform: rotate(20deg);
  }
  45% {
    transform: rotate(-20deg);
  }
  60% {
    transform: rotate(12deg);
  }
  70% {
    transform: rotate(-12deg);
  }
  80% {
    transform: rotate(6deg);
  }
  88% {
    transform: rotate(-6deg);
  }
  93% {
    transform: rotate(3deg);
  }
  97% {
    transform: rotate(-3deg);
  }
  98% {
    transform: rotate(2deg);
  }
  99% {
    transform: rotate(-2deg);
  }
  99.5% {
    transform: rotate(1deg);
  }
  to {
    transform: rotate(0);
  }
}
@keyframes animeShake {
  25% {
    transform: rotate(20deg);
  }
  45% {
    transform: rotate(-20deg);
  }
  60% {
    transform: rotate(12deg);
  }
  70% {
    transform: rotate(-12deg);
  }
  80% {
    transform: rotate(6deg);
  }
  88% {
    transform: rotate(-6deg);
  }
  93% {
    transform: rotate(3deg);
  }
  97% {
    transform: rotate(-3deg);
  }
  98% {
    transform: rotate(2deg);
  }
  99% {
    transform: rotate(-2deg);
  }
  99.5% {
    transform: rotate(1deg);
  }
  to {
    transform: rotate(0);
  }
}
@-webkit-keyframes animeShrink {
  35% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.4);
  }
  80% {
    transform: scale(0.95);
  }
  93% {
    transform: scale(1.1);
  }
  98% {
    transform: scale(0.98);
  }
  to {
    top: 0px;
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
@keyframes animeShrink {
  35% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.4);
