.code-field-root {
  display: flex;
  position: relative;
}
.code-field-root:hover .run-btn {
  opacity: 1;
  visibility: visible;
}
.code-field-root .CodeMirror {
  height: auto;
  flex: 1;
  background-color: rgba(0, 0, 0, 0.05);
}
.code-field-root .CodeMirror .CodeMirror-linenumber {
  padding-right: 5px;
  color: #999;
  background-color: #e6e6e6;
}
.code-field-root .run-btn {
  display: inline;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  padding: 5px 14px;
  background-color: green;
  color: #fff;
  font-size: 14px;
  line-height: 1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.15s ease-in;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.code-field-root .presentation-zone {
  max-width: 50%;
  max-height: 100%;
  flex: 1;
  display: flex;
  position: relative;
  margin-left: 10px;
  background-color: rgba(0, 0, 0, 0.05);
  overflow: scroll;
  align-items: center;
  justify-content: center;
}
.code-field-root .presentation-zone::after {
  content: attr(data-id);
  font-size: 45px;
  color: rgba(0, 0, 0, 0.05);
  line-height: 0;
}
