.hover-flash {
  -webkit-animation: hover-flash infinite 1s ease-in-out;
          animation: hover-flash infinite 1s ease-in-out;
}
.click-flash {
  -webkit-animation: click-flash infinite 1s ease-in-out;
          animation: click-flash infinite 1s ease-in-out;
}
.out-line {
  outline: 1px solid;
}
a {
  color: inherit;
}
@-webkit-keyframes hover-flash {
  from,
  to {
    background-color: transparent;
  }
  50% {
    background-color: greenyellow;
  }
}
@keyframes hover-flash {
  from,
  to {
    background-color: transparent;
  }
  50% {
    background-color: greenyellow;
  }
}
@-webkit-keyframes click-flash {
  from,
  to {
    background-color: transparent;
  }
  50% {
    background-color: yellow;
  }
}
@keyframes click-flash {
  from,
  to {
    background-color: transparent;
  }
  50% {
    background-color: yellow;
  }
}
